<template>
  <div v-title="''">
    <div class="clearfix">
      <span>
        <span></span>
        <span>{{ orderData.purchase_contract_no }}</span>
      </span>
      <span class="float-r">
        <span></span>
        <span>{{ orderData.protocol }}</span>
      </span>
    </div>
    <data-table v-if="!isView" :data="tableData"/>
    <div class="mg-t-20">
      <div class="oil-card">
        <div class="oil-card-info oil-row">
          <span></span>
        </div>
        <div class="oil-card-box oil-row">
          <el-row>
            <el-col>
              <span class="oil-bold">{{ pageData.oil_type }}</span>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.quantity }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.tradeTerm }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.tolerance }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">
                  <a :href="QSfilePath" target="_blank" rel="noopener noreferrer">{{ pageData._filePath }}</a>
                </el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.pricingFormula }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.pricingPeriod }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.quantityDter }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.paymentTerm }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.law }}</el-col>
              </el-row>
            </el-col>
            <el-col>
              <el-row>
                <el-col :span="2"></el-col>
                <el-col :span="22">{{ pageData.inspection }}</el-col>
              </el-row>
            </el-col>
            <el-col>
              <el-row>
                <el-col :span="2"></el-col>
                <el-col :span="22">{{ pageData.otherTerm }}</el-col>
              </el-row>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="oil-card">
        <div class="oil-card-info oil-row">
          <span></span>
        </div>
        <div class="oil-card-box oil-row">
          <el-row :gutter="10">
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.transportMode }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.laycanDateAsString }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.laytimeAsString }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.demurrageRateNum }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row v-if="isUnload">
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ loadPort }}</el-col>
              </el-row>
              <el-row v-else>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ unloadPort }}</el-col>
              </el-row>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="oil-card">
        <div class="oil-card-info oil-row">
          <span></span>
        </div>
        <div class="oil-card-box oil-row">
          <el-row>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.salerName }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.salerEmail }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.salerAddress }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.salerFax }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.salerTraderName }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.salerTelephone }}</el-col>
              </el-row>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="oil-card">
        <div class="oil-card-info oil-row">
          <span></span>
        </div>
        <div class="oil-card-box oil-row">
          <el-row>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.buyerName }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.buyerEmail }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.buyerAddress }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.buyerFax }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.buyerTraderName }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.buyerTelephone }}</el-col>
              </el-row>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="oil-card">
        <div class="oil-card-info oil-row">
          <span></span>
        </div>
        <div class="oil-card-box oil-row">
          <el-row>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.agentName }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.agentEmail }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.agentContactAddress }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.agentFax }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.agentContactName }}</el-col>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="4"></el-col>
                <el-col :span="20">{{ pageData.agentContactPhone }}</el-col>
              </el-row>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import API from "@/api/api";
import { mapGetters } from "vuex";
import { fmtPageData, reqCertificate } from "@/util/index";
import DataTable from "@/common/dataTable";

export default {
  components: {
    DataTable
  },
  data() {
    return {
      orderOptions: [],
      tableData: [],
      QSfilePath: "",
      pageData: {
        oil_type: "",
        quantity: null,
        tradeTerm: "",
        tolerance: "",
        qualityStandard: "",
        pricingFormula: "",
        pricingPeriod: "",
        paymentTerm: "",
        quantityDter: null,
        inspection: "",
        law: "",
        otherTerm: "",
        transportMode: "",
        loadCountry: "",
        loadPort: "",
        laycanDateAsString: "",
        laytimeAsString: "",
        demurrageRateNum: "",
        dischargeCountry: "",
        dischargePort: "",
        salerName: "",
        salerEmail: "",
        salerAddress: "",
        salerFax: "",
        salerTraderName: "",
        salerTelephone: "",
        buyerName: "",
        buyerEmail: "",
        buyerAddress: "",
        buyerFax: "",
        buyerTraderName: "",
        buyerTelephone: "",
        agentName: "",
        agentEmail: "",
        agentContactAddress: "",
        agentFax: "",
        agentContactName: "",
        agentContactPhone: ""
      }
    };
  },
  computed: {
    ...mapGetters(["deal_no", "orderData", "blockData"]),
    isView() {
      return this.$route.query.isView;
    },
    loadPort() {
      return this.pageData.loadCountry + " " + this.pageData.loadPort;
    },
    unloadPort() {
      return this.pageData.dischargeCountry + " " + this.pageData.dischargePort;
    },
    isUnload() {
      return this.pageData.tradeTerm.toUpperCase() === "FOB";
    }
  },
  watch: {
    orderData(val) {}
  },
  methods: {
    reqPageData(deal_no) {
      this.$http(API.TRADE, { deal_no }, "get").then(res => {
        let odata = res.data;
        if (odata) {
          let pdata = fmtPageData(odata);
          this.pageData = this.fmtQualityStandard(pdata.pageData);
          // console.log(this.pageData);
          let tdata = pdata.attachmentList;
          this.$store.commit("setTableData", tdata);
        }
      });
    },
    fmtQualityStandard(data) {
      let qs = data.qualityStandard;
      let fn = qs.split("/").pop();
      data._filePath = fn;
      data.oil_type = data.productCategory;
      if (qs) this.QSfilePath = API.HOST_STATIC + "/" + qs;
      return data;
    }
  },
  mounted() {
    let deal_no = this.deal_no;
    // if (this.isView) {
    //   this.pageData = this.blockData;
    // } else {
      this.reqPageData(deal_no);
    // }
  }
};
</script>



