        goodsinfoDistrictcode: null,
        contactperson: null,
        contactway: null,
        uuid: null,
        createtime: null,
        blockchainId: null
      },
      tableData: []
    };
  },
  computed: {
    ...mapGetters(["deal_no"]),
    isView() {
      return this.$route.query.isView;
    }
  },
  methods: {
    reqPageData() {
      let oid = {
        deal_no: this.deal_no
      };
      this.$http(API.BROKER, oid, "get").then(res => {
        let odata = res.data;
        if (odata) {
          let pdata = fmtPageData(odata);
          this.pageData = odata.pageData;

          let tdata = pdata.attachmentList;
          this.$store.commit("setTableData", tdata);
        }
      });
    }
  },
  mounted() {
    this.reqPageData();
  }
};
</script>



